/*item buttons--done and remove*/
.itembtn{
  display: flex;
  padding: 5px 10px;
  margin-right:100%;
  margin-top: 10px;
  border: 1px solid #480048;
  border-radius: 5px;
  cursor: pointer;
  background-color: #861A86;
  color: white;
  font-size: 15px;
}
.itembtn:hover{
  background: #480048;
  scale:1.15;
  transition: 0.9s;
}
/*the tasks to do */
.todo{
  padding: 10px 10px;
  width:95%;
  margin-bottom: 10px;
  max-width: 100%;
  border:2px solid #480048;
  padding: 5px;
  border-radius: 10px;
  background: #480048;
  transition: all 1.3s ease;

}

.todo:hover{
  transform: translateY(-20px);
  box-shadow: 0px 0px 30px white;
  scale:1.1;
  background: #861A86;
}
ul{
  padding: 10px 10px;
}
@media (max-width: 660px){
  .todo{
    width: 80%;
    margin-left: 25px;
  }
  .todo:hover{
    transform: translateY(0);

  }
}
