body {
  background: linear-gradient(90deg,#480048,#861A86);
  background-size: cover;
  color: #ffffff;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;

}

header {
  padding: 20px;
  text-align: center;
  font-family: cursive;
}

main {
  place-items:center;
  position:relative;
  display: block;
  width: 500px;
  max-width: 600px;
  background:linear-gradient(300deg, #480048, #861A86);
  border: 2px solid #480048;
  border-radius: 10px;
  box-shadow: 0px 0px 70px black;
  transition: 1s ease-in;
}

main:hover{
  transform: translateY(-30px);
  scale:1.1;

}

/*input box*/
.task{
  padding-left:5%;
  width: 90%;
  height: 10%;
}


/* Input form */
input[type="text"] {
  width: 100%;
  height:  10vh;
  padding: 10px;
  margin-bottom: 10px;
  border:none;
  box-shadow:0px 0px 15px white;
  background:transparent;
  color: #ffffff;
  font-size: 20px;
  border-radius: 10px;
  box-sizing: border-box;
}


input[type="text"]::placeholder {
  color: #cccccc;
}

/*add new task button*/
.submitBtn{
  width:100%;
  height: 5vh;
  background: #480048;
  box-shadow: 0px 0px 10px white;
  border: none;
  border-radius: 20px;
  color: white;
  font-size: large;
  margin-top: 10px;
  transition: 1s ease-out;

}
.submitBtn:hover{
  background: #861A86;
  scale:1.1;
}

@media (max-width: 660px ) {
  main:hover{
    transform: translateY(0px);
    scale:1.0;
  }

}
